 650px;\n  overflow: auto;\n}\n/* Table */\ntable {\n  font-family: arial, sans-serif;\n  border-collapse: collapse;\n  width: 100%;\n  font-size: 20px;\n}\ntable td,\ntable th {\n  border: 1px solid #FFFFFF;\n  text-align: center;\n  padding: 15px;\n}\ntable th {\n  width: 210px;\n}\ntable tr:nth-child(even) {\n  background-color: #F9F8FF;\n}\n/* select */\nlabel {\n  font-size: 1rem;\n  padding: 10px;\n}\nselect {\n  border: 1px solid #0046AD;\n  border-radius: 15px;\n  margin-top: 30px;\n  width: 100%;\n  font-size: 1.1rem;\n  padding: 5px;\n  color: white;\n  background-color: #0046AD;\n}\noption {\n  font-weight: bold;\n  padding: 13px;\n  border-bottom: 2px solid #FFFFFF;\n}\n@media screen and (max-width: 900px) {\n  .row {\n    flex-direction: column;\n    text-align: center;\n  }\n}\n.blueDivider {\n  margin-bottom: 0;\n  padding-bottom: 5px;\n  background-color: #0046AD;\n}", "",{"version":3,"sources":["webpack://src/styles.scss"],"names":[],"mappings":"AAAA;;;;CAAA;AAMA,8CAAA;AACA;EACI,SAAA;EACA,UAAA;EACA,sBAAA;AAAJ;AACK;;EAEG,mBAAA;AACR;AACK;EACG,aAAA;AACR;AAGA,sBAAA;AAMA;EAEI,iBAAA;EACA,uBAAA;EACA,6BAAA;EACA,cAAA;EACA,gBARS;EAST,yCAZS;EAaT,8CAAA;EAkBA,oCAAA;EAMA,4BAAA;EAKA,kBAAA;AAhCJ;AAII;;;;;;;;;;;;EAYI,cAAA;AAFR;AAII;EACI,qBAAA;AAFR;AAKI;;EAEI,gBAAA;EACA,qBAAA;AAHR;AAMI;EACI,eAAA;EACA,cAAA;AAJR;AAOI;;;;EAII,aAAA;AALR;AAQA;;;;CAAA;AAMA,gCAAA;AAoBA,+BAAA;AAUA,wBAAA;AAqBA,YAAA;AACA;EAVI,4BAAA;EAYA,WAAA;AAtDJ;AAyDA;EACI,cAzDW;AAGf;AAyDA,uCAAA;AAEA;EAEI,aAAA;EAEA,eAAA;EACA,YAAA;EACA,aAAA;EACA,WAAA;AAvDJ;AA2DA,eAAA;AAEA;EACI,aAAA;EACA,mBAAA;EACA,SAAA;EACA,yBAvEI;EAwEJ,aAAA;EACA,UAAA;EACA,gBAAA;AAzDJ;AA4DA,gBAAA;AACA;EAEI,SAAA;EACA,SAAA;EACA,uBAAA;EACA,aAAA;EACA,aAAA;EACA,cAAA;AAzDJ;AA4DA,UAAA;AACA;EACI,8BAAA;EACA,yBAAA;EACA,WAAA;EACA,eAAA;AAzDJ;AA0DI;;EA7DA,yBAAA;EAgEI,kBAAA;EACA,aAAA;AAxDR;AA0DI;EACI,YAAA;AAxDR;AA0DI;EACI,yBAxGA;AAgDR;AA4DA,WAAA;AACA;EACI,eAAA;EACA,aAAA;AAzDJ;AA4DA;EAjFI,yBAAA;EAmFA,mBAAA;EACA,gBAAA;EACA,WAAA;EACA,iBAAA;EACA,YAAA;EACA,YAAA